import React, { useState, useCallback, useEffect } from 'react';
import { useHistory } from "react-router-dom";
import _ from 'lodash';
import moment from 'moment';
import {
  message
} from 'antd';
import axios from 'axios';

import 'antd/dist/antd.css'; // Import antd

import Layout from '../../components/Layout'
import { SERVER_API_URL } from '../../services';
import { Router } from 'react-router';

const Home = (props) => {
  const history = useHistory();
  const [loading, setLoading] = useState(true);
  const [username, setUsername] = useState('');

  const searchByUsername = () => {
    setLoading(true);
    history.push(`/downloads/${username}`)
  }

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 2000);
  }, []);

  return (
    <Layout loading={loading}>
      <section id="hero" className="d-flex align-items-center">
        <div
          className="container d-flex flex-column align-items-center justify-content-center">
          <a href="/" className="logo mr-auto"><img src="assets/img/logo.png" alt="" className="img-fluid" /></a>

          <h1>Buscá tus descargas de Twitter</h1>
          <h2>Si etiquetaste a <a href="https://twitter.com/vbotcito" target="_blank">vbotcito</a> en un video de <img src="https://cdn2.iconfinder.com/data/icons/metro-uinvert-dock/256/Twitter_NEW.png" style={{ maxWidth: 20, maxHeight: 20 }} />, escribe tu nombre de usuario y te mostraré tus descargas.</h2>

          <div className="form">
            <input
              type="text"
              name="username"
              onChange={(evt) => {
                setUsername(evt.target.value);
              }}></input>
            <input
              type="submit"
              value="Buscar"
              onClick={searchByUsername}
            />
          </div>
          {/* <a href="#about" className="btn-get-started scrollto">Get Started</a> */}
        </div>
      </section>
    </Layout>
  );
}

export default Home;