import React, { useState, useCallback, useEffect } from 'react';
import _ from 'lodash';
import moment from 'moment';
import {
  message,
  Alert
} from 'antd';
import axios from 'axios';

import 'antd/dist/antd.css'; // Import antd

import Layout from '../../components/Layout'
import VideoItemPreview from '../../components/video_item_preview';
import { SERVER_API_URL } from '../../services';
import { useParams } from 'react-router';

const DownloadsList = (props) => {
  const params = useParams();
  const [loading, setLoading] = useState(true);
  const [username, setUsername] = useState('');
  const [videos, setVideos] = useState([]);

  useEffect(() => {
    setUsername(params.username);
    axios.get(`${SERVER_API_URL}/twitter_videos/${params.username}`)
    .then((response) => {
      const { data } = response;
      if(data.success){
        setVideos(data.videos);
      }

      setLoading(false);
    });
  }, []);

  return (
    <Layout loading={loading}>
      <section className="container">
        <a href="/" className="logo mr-auto" style={{ marginTop: 20, display: 'block', marginBottom: 15 }}><img src="/assets/img/logo.png" alt="" className="img-fluid" style={{ maxWidth: 40 }} /></a>

        <h1>Contenido de <span style={{ color: '#3b4ef8' }}>@{ username }</span></h1>
        <p style={{ marginTop: 10, marginBottom: 10, fontWeight: 300 }}>Aquí te dejo el contenido que pediste que descargue, <span style={{ fontWeight: 800 }}>@{username}</span></p>
        <p style={{ marginTop: 0, marginBottom: 20, fontWeight: 300 }}>Recuerda etiquetar en un video de Twitter a <a href="https://twitter.com/vbotcito" target="_blank" style={{ fontWeight: 800 }}>@vbotcito</a> para que tu contenido aparezca aquí.</p>

        <hr />

        <div className="row">
          { videos && videos.map((e) => {
            return (
              <div className="col-lg-4 col-md-6 portfolio-item filter-web">
                <VideoItemPreview
                  tweet={e}
                />
              </div>
            )
          })}

          { !videos || !videos.length &&
            <Alert
              style={{ width: '100%', margin: '0 auto' }}
              type="error"
              message={
                <p style={{ margin: 0, fontSize: 16, fontWeight: 600 }}>Aún no tienes videos descargados. Recuerda etiquetar en algún video de Twitter a <a href="https://twitter.com/vbotcito" target="_blank">@vbotcito</a>, y tu video pronto aparecerá aquí.</p>
              }
              banner
            />
          }
        </div>
      </section>
    </Layout>
  );
}

export default DownloadsList;