import React from 'react';
import _ from 'lodash';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import 'antd/dist/antd.css'; // Import antd

// Components
import Home from '../pages/home';
import DownloadsList from '../pages/downloads_list';

export default class App extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {
        return (
          <Router>
            <Switch>
              <Route
                exact
                path='/'
                component={Home}
              />


              <Route
                path='/downloads/:username'
                component={DownloadsList}
              />
            </Switch>
          </Router>
        );
    }
}